.logo img {
  max-height: 160px;
  padding: 30px;
}
header {
  border-bottom: dashed 2px #efefef;
  margin-bottom: 60px;
}
header p {
  color: #775221;
  font-family: 'Droid Serif', serif;
  font-size: 18px;
}
footer {
  padding: 40px;
  background: #f7f5f5;
}
.product {
  margin-bottom: 70px;
}
.product .imgContainer {
  background: #c2d3e8;
}
.product a {
  color: #775221;
}
.product a img {
  opacity: 0.85;
  width: 100%;
}
.product a:hover img {
  opacity: 1;
}
